@import url(https://fonts.googleapis.com/css?family=Mr+De+Haviland|Open+Sans:300,400,700);
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
* {
  box-sizing: border-box;
  font-weight: 300; }

body {
  font-family: 'Open Sans', sans-serif;
  min-height: 100vh;
  overflow-y: scroll; }

.content {
  padding: 50px; }
  .content > h2 {
    margin-top: 0; }

a, a:focus, a:hover {
  text-decoration: none; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

blockquote {
  background-color: #1E1E1E;
  color: white;
  padding: 20px;
  margin: 5px 0; }
  blockquote p {
    margin-left: 20px; }
  blockquote label {
    display: block;
    margin-bottom: 20px;
    color: orange; }

.notes {
  width: auto;
  display: inline-block;
  padding-left: 0px;
  margin-top: 0; }
  .notes li {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .notes li p {
      background-color: #2b2b2b;
      padding: 2px 10px;
      margin: 2px 0;
      font-weight: normal;
      color: white;
      border-radius: 5px;
      text-decoration: none;
      display: inline; }
    .notes li span {
      margin-left: 25px;
      text-align: right; }

.simpleInput {
  width: 100%;
  max-width: 500px;
  margin-right: 20px;
  padding: 5px;
  border: none;
  border-bottom: 1px dotted black;
  margin-bottom: 10px; }
  .simpleInput:focus {
    outline: none; }

.simpleButton {
  padding: 5px 10px; }

.greenText {
  color: green !important; }

.indent {
  padding-left: 20px; }

.beauty_header {
  background: #232323;
  display: flex;
  align-items: center; }
  .beauty_header .nav1 {
    display: flex;
    padding: 10px; }
    .beauty_header .nav1 h4 {
      display: none; }
    .beauty_header .nav1 a {
      font-weight: lighter;
      padding: 10px;
      letter-spacing: 0.025em;
      margin: 0 10px;
      position: relative;
      color: gray;
      font-size: 14px; }
      .beauty_header .nav1 a:hover {
        color: whitesmoke; }
      .beauty_header .nav1 a.active {
        color: white; }
  .beauty_header .logo_box {
    background: #161616;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 50px;
    font-size: 30px;
    font-family: "Mr De Haviland", cursive;
    height: 60px; }

.splitter {
  display: flex; }
  .splitter > div {
    padding: 0 30px; }
    .splitter > div:first-of-type {
      padding-left: 0; }
    .splitter > div:last-of-type {
      padding-right: 0;
      flex: 1 1; }
    .splitter > div > h2:first-of-type {
      margin-top: 0; }

.hlist a {
  padding: 5px 15px;
  color: black;
  display: block;
  text-align-last: right;
  position: relative; }
  .hlist a.active:after {
    font-weight: 900;
    content: "\F0D9";
    font-family: "Font Awesome 5 Free";
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    color: #494949;
    padding-bottom: 1px; }

